import Layout from "../components/Layout";

const KillerProfile = ({ killer }) => {
  console.log("({killer})", killer);
  return (
    <Layout>
      <div>
        <img src={killer.img} alt="" />
        <table>
          {Object.keys(killer.Profile).map(key => (
            <tr key={key}>
              <td>{key.replace("_", " ")}</td>
              <td>{killer.Profile[key]}</td>
            </tr>
          ))}
        </table>
      </div>
    </Layout>
  );
};

KillerProfile.getInitialProps = async context => {
  const killer = context.query.data;

  console.log("output", killer);

  return {
    killer
  };
};

export default KillerProfile;
